    content: "";
    z-index: 2;
    position: absolute;
    width: 4rem;
    height: 0.3333333333rem;
    transform: scaley(0) rotateZ(0deg);
    background: #ef5350;
    animation: grow3 0.5s forwards 0.5s;
}

