    align-items: center;
    justify-content: center;
    background-color: #ef5350;
    animation: grow 0.5s forwards;
}

@keyframes grow {
    from {
        transform: scale(1);
    }
    to {
        transform: scale(1);
    }
}

.forbidden-sign::before {
    position: absolute;
    background-color: white;
    border-radius: 50%;
    content: "";
    width: 4rem;
    height: 4rem;
    transform: scale(0);
    animation: grow2 0.5s forwards 0.5s;
}

@keyframes grow2 {
    from {
        transform: scale(0);
    }
    to {
        transform: scale(1);
    }
}


/* slash */

.forbidden-sign::after {
    content: "";
    z-index: 2;
    position: absolute;
    width: 4rem;
    height: 0.3333333333rem;
    transform: scaley(0) rotateZ(0deg);
    background: #ef5350;
    animation: grow3 0.5s forwards 0.5s;
}

@keyframes grow3 {
    from {
        transform: scaley(0) rotateZ(0deg);
    }
    to {
        transform: scaley(1) rotateZ(-45deg);
    }
}

.close{
    color: #fff;
}

